<template>
  <div>
    <el-button type="primary" :disabled="loading" @click="handleExportPDF">
      <template v-if="loading">
        <i class="el-icon-loading"></i>
      </template>
      <span v-else>PDF</span>
    </el-button>
    <slot></slot>
  </div>
</template>
<script>
import JsPdf from 'jspdf'
import html2canvas from 'html2canvas'
export default {
  name: 'CExportPdf',
  props: {
    title: {
      type: String,
      default: ''
    },
    className: {
      type: String,
      default: 'pageData'
    },
    titleClassName: {
      type: String,
      default: 'title'
    },
    marginLeft: {
      type: Number,
      default: 5
    }
  },
  data () {
    return {
      loading: false
    }
  },
  methods: {
    handleExportPDF () {
      this.loading = true
      const container = document.querySelector('.' + this.className)
      const contentWidth = container.offsetWidth // 
      let [imgX, imgY] = [595.28, 841.89]
      const pdf = new JsPdf('p', 'pt', 'a4')
      const title = document.querySelector('.' + this.titleClassName)
      const titleHeight = (title.offsetHeight * imgY) / contentWidth // A4
      const pageHeight = (contentWidth / imgX) * imgY - titleHeight
      this.drawImg(title).then(e1 => {
        const pageData = e1.base64Img
        // a4[595.28,841.89]htmlcanvaspdf
        var imgWidth = imgX
        var imgHeight = (imgX / e1.width) * e1.height
        pdf.addImage(pageData, 'png', 0, 0, imgWidth, imgHeight)
        Promise.allSettled(
          Array.from(document.querySelectorAll('.' + this.className)).map(
            (e, i) => {
              return this.drawImg(e)
            }
          )
        ).then(result => {
          result.map((e1, i) => {
            const margin = this.marginLeft // 
            const pageData = e1.value.base64Img // base64
            var leftHeight = e1.value.height // 
            var position = 0 // 
            // a4[595.28,841.89]htmlcanvaspdf
            var imgWidth = imgX - 2 * margin
            var imgHeight = (imgWidth / e1.value.width) * e1.value.height
            // htmlpdf(841.89)
            // pdf
            if (leftHeight < pageHeight) {
              pdf.addImage(
                pageData,
                'png',
                margin,
                i === 0 ? titleHeight : 2,
                imgWidth,
                imgHeight,
                'alias' + i,
                'SLOW'
              )
            } else {
              while (leftHeight > 0) {
                pdf.addImage(pageData, 'png', margin, i === 0 ? titleHeight : position + 2, imgWidth, imgHeight, 'alias' + i, 'SLOW')
                leftHeight -= pageHeight
                position -= imgY
                // 
                if (leftHeight > 0) {
                  pdf.addPage()
                }
              }
            }
            if (i < result.length - 1) {
              pdf.addPage()
            }
          })
          pdf.save(this.title + '.pdf')
          this.loading = false
        })
      })
    },
    drawImg (container) {
      document.documentElement.scrollTop = 0
      document.body.scrollTop = 0
      // const container = document.querySelector('#page1')
      // 
      const width = container.offsetWidth
      const height = container.offsetHeight
      // html2canvas 
      const ops = {
        width,
        height,
        x: 0,
        scale: window.devicePixelRatio * 2,
        y: window.pageYOffset,
        windowHeight: height,
        useCORS: true,
        allowTaint: false
      }
      return html2canvas(container, ops)
        .then(canvas => {
          return {
            base64Img: canvas.toDataURL('image/jpeg', 1.0),
            height,
            width
          }
        })
        .catch(() => '')
    }
  }
}
</script>
